import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {PrivateComponent} from './private/private.component';
import {BusinessComponent} from './business/business.component';
import {BusinessExtraComponent} from './businessExtra/business-extra/business-extra.component';
import {PrivateExtraComponent} from './privateExtra/private-extra/private-extra.component';


const routes: Routes = [
  { path: '', component: PrivateComponent },
  { path: 'business', component: BusinessComponent },
  { path: 'privateExtra', component: PrivateExtraComponent},
  { path: 'businessExtra', component: BusinessExtraComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {
    anchorScrolling: 'enabled',
  })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
